<template>
  <div class="g-bill">
    <div class="m-tab">
      <div :class="{'active':tabs==1}" @click="tabed(1)">积分</div>
      <div :class="{'active':tabs==2}" @click="tabed(2)">元宝</div>
    </div>
    <div class="m-panel scroll-list-wrap">
      <cube-scroll
        ref="scroll"
        :data="list"
        :options="options"
        @pulling-down="onPullingDown"
        @pulling-up="onPullingUp">
        <ul>
          <li v-for="(v,i) in list" class="tab-panel-li" :key="i">
            <span class="time">{{v.createTimeStr}}</span>
            <span class="action">
              {{
              v.type=='1'?'充值':
              v.type=='2'?'消费':
              v.type=='3'?'分成':
              '收入'}}
            </span>
            <span class="number">{{v.money}}</span>
          </li>
        </ul>
      </cube-scroll>
    </div>
  </div>
</template>

<script>

  export default {
    name: "bill",
    data () {
      return {
        tabs:1,
        list:[],
        options:{/*滚动配置*/
          scrollbar:{fade:true},
          pullUpLoad:{txt:{ noMore: '没有更多数据了' }},
          pullDownRefresh:{stopTime:0,txt:' '}
        },
        bit:{
          username:sessionStorage.getItem('userid'),
          page:1,
          pageSize:100,
          currency:1
        }
      }
    },
    created(){
      this.jf();//积分
    },
    methods:{
      tabed(v){
        this.tabs=v;
        this.bit.currency=v;
        this.bit.page=1;
        this.list=[];
        if(v==1){
          this.jf()
        }else{
          this.yb()
        }
      },
      onPullingDown() {
        this.bit.page=1;
        this.list=[];
        if(this.bit.currency==1){
          this.jf()
        }else{
          this.yb()
        }
      },
      onPullingUp() {
        if(this.bit.currency==1){
          this.jf()
        }else{
          this.yb()
        }
      },
      jf(){
        this.bit.currency=1;
        this.$post(this.$bee+'/api/user/listBill',this.bit).then((data)=> {
          if (data.code == 200&&data.data.records.length!=0) {
            this.list=data.data.records;
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      yb(){
        this.bit.currency=2;
        this.$post(this.$bee+'/api/user/listBill',this.bit).then((data)=> {
          if (data.code == 200&&data.data.records.length!=0) {
            this.list=data.data.records;
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
.g-bill
  .m-tab
    text-align center
    div
      display inline-block
      width 175px
      height 30px
      line-height 30px
      box-sizing border-box
      font-size 19px
      color #fff
      background-color: #c0c0c0;
      padding 0
      &:nth-child(1)
        border-radius: 3px 0px 0px 3px;
      &:nth-child(2)
        border-radius: 0px 3px 3px 0px;
      &.active
        background-color: #fb5757;
  .m-panel
    margin-top 40px
    ul
      padding 0 13px
      li
        text-align left
        height 32px
        line-height 32px
        font-size 13px
        color: #797979;
        border-bottom 1px solid #e6e6e6
        .action
          margin-left 80px
        .number
          margin-left 68px
  &>>>.cube-tab-bar
    flex-wrap wrap
    .cube-tab
      width 175px
      height 30px
      line-height 30px
      box-sizing border-box
      font-size 19px
      color #fff
      background-color: #c0c0c0;
      padding 0
      flex none
      &:nth-child(1)
        border-radius: 3px 0px 0px 3px;
      &:nth-child(2)
        border-radius: 0px 3px 3px 0px;
      &.cube-tab_active
        background-color: #fb5757;
  &>>>.cube-tab-panels
    margin-top 20px
    ul
      padding 0 13px
    .tab-panel-li
      text-align left
      height 32px
      line-height 32px
      font-size 13px
      color: #797979;
      border-bottom 1px solid #e6e6e6
      .action
        margin-left 80px
      .number
        margin-left 68px
</style>
